'use client';

import { useState } from 'react';
import Image from 'next/image';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { verifyDocumentAction } from '@/app/actions';
import type { VerifyLandDocAuthenticityOutput } from '@/ai/flows/verify-land-doc-authenticity';
import { Loader2, CheckCircle, XCircle, Upload, Volume2, VolumeX } from 'lucide-react';
import { Progress } from './ui/progress';
import { useSpeech } from '@/hooks/use-speech';
import { useLanguage } from '@/contexts/language-context';
import { cn } from '@/lib/utils';

export default function DocVerificationForm() {
  const [file, setFile] = useState<File | null>(null);
  const [preview, setPreview] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<VerifyLandDocAuthenticityOutput | null>(null);
  const { toast } = useToast();
  const { speak, stop, isSpeaking } = useSpeech();
  const { t, language } = useLanguage();
  const [speakingSection, setSpeakingSection] = useState<string | null>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    if (selectedFile) {
      setFile(selectedFile);
      setResult(null);

      const reader = new FileReader();
      reader.onloadend = () => {
        setPreview(reader.result as string);
      };
      reader.readAsDataURL(selectedFile);
    }
  };

  const handleSpeak = (section: string, text: string) => {
    if (isSpeaking && speakingSection === section) {
      stop();
      setSpeakingSection(null);
    } else {
      speak({ text, lang: language });
      setSpeakingSection(section);
    }
  };


  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!file) {
      toast({
        title: t('noFileSelectedTitle', 'docVerification'),
        description: t('noLandDocFileSelectedDesc', 'docVerification'),
        variant: 'destructive',
      });
      return;
    }

    setIsLoading(true);
    setResult(null);

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = async () => {
      const photoDataUri = reader.result as string;
      try {
        const verificationResult = await verifyDocumentAction({ photoDataUri });
        setResult(verificationResult);
        const resultText = `${t('verificationResult', 'docVerification')}: ${verificationResult.isAuthentic ? t('authentic', 'docVerification') : t('notAuthentic', 'docVerification')}. ${t('confidenceScore', 'docVerification')}: ${(verificationResult.confidenceScore * 100).toFixed(0)}%. ${t('aiReasoning', 'docVerification')}: ${verificationResult.reason}`;
        handleSpeak('result', resultText);
      } catch (error) {
        console.error('Verification failed:', error);
        toast({
          title: t('verificationFailedTitle', 'docVerification'),
          description: t('verificationFailedDesc', 'docVerification'),
          variant: 'destructive',
        });
      } finally {
        setIsLoading(false);
      }
    };
  };

  const getConfidenceColor = (score: number) => {
    if (score < 0.5) return 'text-destructive';
    if (score < 0.8) return 'text-yellow-500';
    return 'text-green-500';
  }

  return (
    <Card className="w-full max-w-2xl mx-auto">
      <form onSubmit={handleSubmit}>
        <CardHeader>
          <div className="flex justify-between items-center">
            <div>
                <CardTitle>{t('landDocVerificationTitle', 'docVerification')}</CardTitle>
                <CardDescription>
                    {t('landDocVerificationDesc', 'docVerification')}
                </CardDescription>
            </div>
            <Button variant="outline" size="icon" onClick={() => handleSpeak('description', t('landDocVerificationDesc', 'docVerification'))}>
                {isSpeaking && speakingSection === 'description' ? <VolumeX className="h-5 w-5" /> : <Volume2 className="h-5 w-5" />}
                <span className="sr-only">{isSpeaking && speakingSection === 'description' ? t('stopReading', 'common') : t('readPageContent', 'docVerification')}</span>
            </Button>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="document-upload">{t('landDocPhoto', 'docVerification')}</Label>
            <div className="flex items-center gap-4">
              <Input id="document-upload" type="file" accept="image/*" onChange={handleFileChange} className="flex-grow" />
            </div>
          </div>

          {preview && (
            <div className="relative w-full h-64 border-2 border-dashed rounded-lg flex items-center justify-center">
              <Image src={preview} alt={t('landDocPreviewAlt', 'docVerification')} fill style={{ objectFit: 'contain' }} className="rounded-lg p-2" />
            </div>
          )}
        </CardContent>
        <CardFooter className="flex-col items-start gap-4">
          <Button type="submit" disabled={isLoading || !file} className="w-full sm:w-auto">
            {isLoading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Upload className="mr-2 h-4 w-4" />}
            {t('verifyDocument', 'docVerification')}
          </Button>
          
          {isLoading && <p className="text-sm text-muted-foreground">{t('aiAnalyzing', 'docVerification')}</p>}

          {result && (
            <Card className="w-full bg-secondary">
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  {result.isAuthentic ? <CheckCircle className="text-green-500" /> : <XCircle className="text-destructive" />}
                  {t('verificationResult', 'docVerification')}: {result.isAuthentic ? t('authentic', 'docVerification') : t('notAuthentic', 'docVerification')}
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label>{t('confidenceScore', 'docVerification')}</Label>
                  <div className="flex items-center gap-2">
                    <Progress value={result.confidenceScore * 100} className="w-full"/>
                    <span className={cn('font-bold', getConfidenceColor(result.confidenceScore))}>
                      {(result.confidenceScore * 100).toFixed(0)}%
                    </span>
                  </div>
                </div>
                <div>
                  <Label>{t('aiReasoning', 'docVerification')}</Label>
                  <p className="text-sm text-muted-foreground p-3 bg-background rounded-md border">{result.reason}</p>
                </div>
              </CardContent>
            </Card>
          )}
        </CardFooter>
      </form>
    </Card>
  );
}
